:root {
    --header-color: #000000;
    --text-hovered-color: #44aada;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--header-color);
    padding: 8px 12px;
    border: solid white;
}

.top button {
    display: none;
}

.header a {
    text-decoration: none;
    color: white;
    font-family: 배달의민족 도현, sans-serif;
}

.menu {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.navbar, .navbtn {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0;
    padding-left: 0;
    list-style: none;
}

.navbar {
    width: 400px;
    padding: 0;
}

.navbar a {
    padding: 10px 70px;
    width: 100%;
    text-align: center;
    font-size: 16px;
}

.navbar a:hover {
    background-color: #444444;
    border-radius: 3px;
    color: var(--text-hovered-color);
}

.navbtn {
    justify-content: space-between;
    width: 200px;
    max-width: 350px;
    padding: 0 40px;
}

.navbtn a {
    padding: 10px 15px;
    width: 20px;
    font-size: 12px;
}

.navbtn a:hover {
    color: var(--text-hovered-color);
}

@media screen and (max-width: 986px) {
    .navbar {
        width: 200px;
        padding: 10px 10px;
    }

    .navbar a {
        padding: 10px 30px;
    }

    .navbtn {
        width: 150px;
        padding: 0;
    }

    .navbtn a {
        padding: 10px 5px;
    }
}

@media screen and (max-width: 700px) {
    .header, .menu {
        flex-direction: column;
        align-items: center;
        margin: 0;
    }

    .top {
        display: flex;
        width: 100%;
        justify-content: center;
    }

    .top img {
    }

    .top button {
        display: flex;
        background-color: black;
        padding: 0;
        border: none;
        width: 36px;
        height: 39px;
        position: absolute;
        margin-top: 15px;
        right: 3%;
    }

    .menu {
        display: none;
    }

    .menu.active {
        display: flex;
    }

    .navbar {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .navbar li {
        width: 100%;
    }

    .navbar a {
        padding: 10px 0;
        width: 100%;
        text-align: center;
        display: inline-block;
    }

    .navbtn {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 10px 0;
    }
}